<template>
  <module-shell title="" height="calc(50% - 24px)">
    <div class="basic-management__content">
      <div class="menu-item"
           v-for="item in menuItem"
           :key="item.title"
           @click="handleRouter(item)"
      >
        <img :src="item.img" alt="">
        <p>{{ item.title }}</p>
      </div>
    </div>

    <el-dialog
        title="Excel"
        :visible.sync="dialogVisible"
        width="40%"
        :before-close="handleClose">
      <img src="./img.png" alt="" width="100%">

    </el-dialog>
  </module-shell>
</template>

<script>
import moduleShell from "../../components/module-shell";

export default {
  name: "index",

  // 
  components: {
    moduleShell,
  },

  // 
  props: {},

  // 
  data() {
    return {
      dialogVisible: false,
      menuItem: [
        {
          img: require('../../images/jgxxgl.png'),
          title: '',
          route: '/institution-information'
        },
        {
          img: require('../../images/xxglxt.png'),
          title: ''
        },
        {
          img: require('../../images/bbzt.png'),
          title: '',
          route: '/bbzt'
        },
        {
          img: require('../../images/xxcj.png'),
          title: 'Excel'
        },
        {
          img: require('../../images/sjjs.png'),
          title: ''
        },
        {
          img: require('../../images/zcfg.png'),
          title: '',
          route: '/zcfg'
        },
      ]
    };
  },

  methods: {
    handleRouter(item) {
      if (item.route) this.$router.push(item.route);

      if(item.title === "Excel"){
        this.dialogVisible = true
      }
    },

    handleClose(done) {
      this.dialogVisible = false
    }
  }
};
</script>

<style lang="less" scoped>
.basic-management__content {
  display: flex;
  flex-wrap: wrap;
  height: calc(100% - 35px);
  padding: 16px 16px 0;

  .menu-item {
    width: 100% / 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 16px;
    color: #333333;

    img {
      width: 40%;
      margin-bottom: 8px;
    }

    p {
      font-size: 12px;
    }
  }

  .menu-item:hover {
    color: #0cf !important;
    cursor: pointer;
  }
}
</style>